.section {
  &--services-expertise {
    display: flex;
    align-content: center;
    align-content: center;
    justify-content: center;
    position: relative;
    width: 100%;
    max-width: 100%;
    flex: 0 0 auto;

    @include mq('tablet-wide') {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}

.article {
  &--services-expertise-intro {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;

    @include mq('tablet-wide') {
      max-width: 83%;
      width: 83%;
      flex: 0 0 auto;
    }

    @include mq('desktop-hd') {
      max-width: 83%;
      width: 83%;
    }


  }
}


.article--services-splash {
  justify-content: center;
  align-items: center;

  @include mq('tablet-wide') {
    justify-content: flex-start;
  }

}

.services-splash__pagination {
  align-self: flex-start;

  @include mq('tablet') {
    // @include vr($margin-top:12);
  }
}

.services-splash__container {
  height: 95vh;
  padding-top: 12%;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  // align-items: center;
  max-width: 90%;
  z-index: 2;
  flex: 0 1 auto;

  @include mq('tablet') {
    max-width: 83%;
    width: 50%;

    .pagination {
      margin-bottom: unset;
    }

    .services-splash__link--jump {
      align-self: flex-start;

      @include mq('tablet') {
        @include vr($margin-top:12);
        align-self: flex-start;
        width: 40%;
      }
    }
  }

  @include mq('tablet-wide') {
    padding-top: 0;
    max-width: 80%;
    width: 40%;
    margin-left: 16%;
    justify-content: center;

    .pagination {}
  }

  @include mq('desktop') {
    max-width: 35%;
    width: 35%;
    margin-left: 8%;
  }

}

.pagination--services-splash {
  @include mq('tablet') {
    margin-bottom: unset;
  }
}


.services-splash__container{
    .title{
        color: $white;
      
        display: flex;
        min-height: .25em;
        flex: 0 1 auto;
        flex-direction: column;
        letter-spacing: .025rem;
        position: relative;
        @include vr($font-size: 6, $line-height: 2);
        @include mq('tablet-wide'){
          @include vr($font-size: 6, $line-height: 2);
        };
        @include mq('desktop'){
        @include vr($font-size: 7, $line-height: 4);
        }
      }
}



.br__services-splash{
@include vr($line-height: 1);
}


.services-splash__sub {
  color: $white;
  max-width: 90%;
  font-family: $font-family-sans-serif;
  font-style: none;
  display: flex;
  flex-direction: row;
  min-height: 1em;
  flex: 0 1 auto;
align-items: center;
border-top: .75rem solid $white;


  @include mq('tablet') {
    max-width: 90%;
  }

  @include mq('tablet-wide') {
    min-height: 2em;
    font-size: 2.15rem;
  }
}

.services-splash__separator{
  all: inherit;
  color: $accent;
  flex: 1 1 auto;
  max-width: 5%;
  text-align: center;
  justify-content: center;
  border: none;

}

.services-splash__image-container {
  background-color: rgba($color: #000000, $alpha: .8);
  overflow: hidden;
  width: 100%;

  img {
    mix-blend-mode: overlay;
    position: absolute;
    max-width: 300%;


    @media only screen and (max-height: 500px) {
      top: -25%;
      left: -152%;
      width: 275%;
    }

    max-width: 500%;
    transform: translate(50%, 0%);
    top: 0;
    left: -193%;
    width: 300%;

    @include mq('tablet') {
      top: 0;
      left: -161%;
      width: 200%;
    }

    @include mq('tablet-wide') {
      top: -30%;
      left: -110%;
      width: 150%;
    }

    @include mq('desktop') {
    width: 110%;
    left: -23%;
    top: -74%;
    transform: translate(21%,37%);
    }

    @include mq('desktop-wide') {
      width: 110%;
      left: -23%;
      top: -115%;
      transform: translate(21%,37%);
      }
  }
}



