  @include mq('desktop') {
    background: $off-white;
  }

  .flex-container {

    .flex-item__content {
      order: 2;
      z-index: 3;

      @include mq('desktop') {
        flex: 1;
        flex-direction: column;
        padding: 0% 3%;
