  }

  li {
    @include vr($font-size: 14px, $margin-top:.5, $margin-bottom:.5);
    @include mq('desktop'){
    @include list__subhead();
    @include vr($margin-top: .25, $line-height: 1.25);
    }
  }

  .link__text {
    text-align: center;
    align-self: flex-start;
    margin-bottom: auto;
    min-height: 1em;
    width: 100%;
    max-width: 100%;
    color: $deep-gray;
    @include pill();
    @include vr($margin-top: 2, $padding: .5 1);
    background: $white;
  }
