@include mq('tablet-wide'){
  background: none;
}

  .pagination {
    @include vr($padding: 3 4);

    @include mq('tablet') {
      @include vr($padding: 6 0 1 0);
    }
  }

  .headline {
    font-weight: 600;
    letter-spacing: -.15rem;
    text-transform: capitalize;
    margin: 0 auto;
    @include vr($padding: 4 2);

    @include mq('tablet') {
      @include vr($padding: 1 0 3 0);
    }
    @include mq('desktop'){
      text-align: center;
    }
  }
}

.commitment:after{
  @include mq('tablet-wide'){
    box-shadow: none;
  }
}

.commitment {
position: relative;
width: 100%;
height: 100%;
display: none;

@include mq('desktop'){
display: block;
  height: 275em;
}
.background__image--cover{
    position: fixed;
  width: 100%;
     height: 100%;
     z-index: -1;
    img{
    position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
      display: block;

      }
}
  .container{
  max-width: 83%;
  background: $dirty-white;
@include mq('tablet-wide'){
  @include dark-shadow();
  @include vr($padding: 6 2 6 2);
  margin-top: 20em;

}
  @include mq('desktop'){
    max-width: 64%;
    @include vr($padding: 6 2 6 2);
    display: flex;
    flex: 0 0 auto;
    flex-wrap: wrap;
    margin-top: 60em;
  }
}
  .pagination,
  .headline {
    max-width: 50%;

    
      margin: 0 auto;
    

    @include mq("tablet-wide") {
      width: 100%;
flex: 0 0 auto;
max-width: 64%;
    }

    @include mq("desktop") {
      width: 61%;
    }

    @include mq("desktop-hd") {
      width: 100%;
    }
  }
}


.commitment__content_container {
  max-width: 83.82%;

  @include mq("tablet") {
    @include vr($width: 32);
  }

  @include mq("tablet-wide") {
    width: 61%;
  max-width: 61%;
    display: flex;
  flex-wrap: wrap;
justify-content: center;
align-items: cneter;
  }

  @include mq("desktop") {
    max-width: 50%;
    flex: 1 1 auto;
    display: flex;
  }

  @include mq("desktop-hd") {
    width: 64%;
  }

  @include vr($margin: 0 auto);
}

.commitment__image-container{
  @include mq('tablet-wide'){
  flex: 1 1 auto;
  position: relative;
  max-width: 50%;
  display: flex;
  height: 64em;
  img{
    overflow: hidden;
    display: block;
    position: absolute;
    top: 0;
    right: -71%;
    max-width: 300%;
    width: 300%;
    height: auto;
  }
}

}

