  // flex: 0 0 auto;
  // flex-wrap: wrap;

}


.commitment__subhead {
  @include body-text-lg();
  font-weight: 500;
  text-transform: lowercase;
  letter-spacing: -.05rem;
  color: $accent;
  font-family: $font-family-serif;
  text-align: center;
  font-style: italic;
  display: inline-block;
}


p.commitment__body_copy {

  @include body-text-md();
  @include vr($line-height: 3);
  font-family: $font-family-serif;
  vertical-align: unset;

  .leader {
    font-size: 3.5rem;
    line-height: 4.5rem;
    font-weight: 400;
    font-family: $font-family-sans-serif;
    color: $gray-700;
    font-variant-caps: all-small-caps;
    vertical-align: unset;
    padding-right: .75rem;

  }

  @include mq('tablet-wide') {}
}


#services__cta > div > h1, #services > div.content__container.services-list__content-container > div.container__headline--overlay.services-list__container-headline > h2, #homepage__cta > article > div > h1{
   color: $black;
}


