// ==========================================================================
// category history
// ==========================================================================
.categories__content_caption {
    opacity: 0;
    -webkit-transition: opacity .5s ease-in-out;
    -moz-transition: opacity .5s ease-in-out;
    -ms-transition: opacity .5s ease-in-out;
    -o-transition: opacity .5s ease-in-out;
    background: rgba($deep-primary, $alpha: .8);
    @include make-row;
    color: $white;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    // flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    @include vr($padding: 0 0);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    .categories__overlay_headline {
        font-weight: 400;
        @include vr($margin: 0 0 0 0);
        // flex: 1;
        display: flex;
        width: 90%;
        min-height: 2em;
        text-align: center;
        justify-content: center;
    }
    .categories__overlay_subtext {
        max-width: 74%;
        @include vr($margin: 0 0 0 0);
        // flex: .8;
        display: flex;
        min-height: 1em;
        text-align: center;
    }
}

.background__categories_content {
    text-align: center;
    position: relative;
    &:hover {
        .categories__content_caption {
            opacity: 1;
        }
    }
}

.categories__content_image {
    width: 100%;
    height: auto;
    // &:hover{
    //   .categories__content_caption{
    //     opacity: 1;
    //   }
    // }
}

