    padding-left: 0px;
    padding-right: 0px;
    h2,
    h3 {
        margin-bottom: 0;
    }
}

.header-25x75row {
    position: relative;
    &__container {
        min-height: 1px;
        flex: 0 0 100%;
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        @include mq("tablet") {
            align-items: center;
        }
        @include mq("tablet-wide") {
            width: 30%;
            max-width: 30%;
        }
    }
    &__row1 {
        flex: 0 0 25%;
        display: flex;
        &--jumbo-digit {
            @include vr($font-size: 5, $line-height: 4);
            font-weight: 600;
            @include mq("tablet") {
                flex: 0 0 20%;
                @include vr($font-size: 6);
            }
        }
    }
    &__row2 {
        flex: 1;
        display: flex;
        &--custom-headline {
            @include vr($font-size: 3, $line-height: 2, $padding-left: 1);
            font-weight: 600;
            letter-spacing: -.025em;
            @include mq("tablet") {
                @include vr($font-size: 4, $line-height: 3, $padding-left: 1);
            }
        }
    }
}

.objectives-header--squeeze {
    @include mq("phone", $type: max) {
        >.header-25x75row__row1--jumbo-digit {
            margin-left: -.15em;
        }
        >.header-25x75row__row2--custom-headline {
            padding-left: .5em;
        }
    }
}

.jumbo-digit--01-tweak {
    @include mq("tablet") {
        letter-spacing: -.05em;
    }
}

.jumbo-digit--snuggie {
    letter-spacing: -.05em;
    @include mq("tablet") {
        letter-spacing: -.05em;
    }
}

