@include mq("tablet-wide") {
    //
    // justification panels (see below for diagrams)
    //
    .justification--flex-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .justification__container_objectives,
    .justification__container_optimize {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .justification__container_modernize,
    .justification__container_grow {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    .page__header-text_container--grow,
    .diagram__modern-marketing,
    .page__header-text_container--modernize {
        @include make-col-ready;
        @include make-col(6);
        padding-left: 0px;
        padding-right: 0px;
    }
    .page__header-text_container--grow,
    .page__header-text_container--modernize {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .page__header-text_container {
        margin-top: 0;
        margin-bottom: 0;
    }
    .justification__page-header_text {
        @include vr($margin-top: 0, $margin-bottom: 0);
    }
    .justification__page-header_text--square {
        width: 83.82%;
        //     text-align: justify;
        margin-left: auto;
        margin-right: auto;
        letter-spacing: .05em;
    }
    .read-more__container {
        @include make-col-ready;
        @include make-col(12);
        padding-left: 0px;
        padding-right: 0px;
    }
    .moreless-button {
        @include make-col-ready;
        @include make-col(12);
        padding-left: 0px;
        padding-right: 0px;
    }
    .justification__conclusion {
        @include make-col-ready;
        @include make-col(12);
        padding-left: 0px;
        padding-right: 0px;
        align-items: flex-start;
    }
    //
    // justification diagrams tablet-wide
    //
    .diagram__objectives {
        display: flex;
        flex-direction: row;
        max-width: 100%;
        width: 100%;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        @include vr($margin-top: 0, $margin-bottom: 0);
    }
    .diagram__content_container {
        margin-left: -1em;
        flex-direction: row;
        flex-wrap: nowrap;
        flex-direction: column;
        width: 24%;
        max-width: 24%;
        padding-left: 0px;
        padding-right: 0px;
        justify-content: center;
        align-items: center;
        flex: 0 0 auto;
        @include vr($margin-top: 0, $margin-bottom: 0);
    }
    .diagram-item__container--77row {
        width: 83.82%;
        max-width: 83.82%;
        height: auto;
        flex: 0 0 auto;
        @include mq('desktop') {
            min-height: 23rem;
            margin-top: 0;
        }
    }
    .objectives-offset {
        display: none;
    }
    .diagram--superior-value {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        svg {
            width: 40em;
        }
    }
    .diagram__modern-marketing {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 50em;
    }
    .diagram__modern-marketing .diagram-content {
        display: block;
        margin-left: -2em;
    }
    .diagram__modern-marketing {
        svg {
            width: 50em;
        }
    }
    .diagram-content__superior-value_svg {
        // margin-left: auto;
        // margin-right: 0;
    }
}

@include mq("desktop") {
    .end-to-end--horz {
        width: 100%;
        .optimize-diagram__container {
            @include make-row;
            margin-left: 0px;
            margin-right: 0px;
            flex-direction: row;
            width: 100%;
            max-width: 100%;
        }
    }
}