}

.article__homepage-placeholder {
    position: relative;
    background: #333;
    color: $white;
    display: flex;
    width: 100%;
    max-width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    // height: 150vh;
    flex-wrap: wrap;
    min-height: 100em;
    @include vr($padding: 6 0);
    @include mq('desktop') {
        min-height: 100vh;
    }
}

.header__container--placeholder,
.placeholder__body-container {
    z-index: 2;
}

.header__container--placeholder {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    flex: 0 0 auto;
    max-width: 90%;
    width: 90%;
    @include mq('tablet') {
        width: 83%;
        max-width: 83%;
    }
    @include mq('tablet-wide') {
        max-width: 50%;
        width: 50%;
    }
}

.pagination__placeholder {
    margin-bottom: unset;
    flex: 0 0 auto;
    max-width: 100%;
    text-align: center;
    align-self: center;
}

.title.headline__header--placeholder {
    text-align: center;
    max-width: 100%;
    width: 100%;
}

.deck__header--placeholder {
    flex: 0 0 auto;
    max-width: 100%;
    width: 100%;
    text-align: center;
    display: flex;
    text-align: center;
    justify-content: center;
    text-transform: lowercase;
    @include vr($margin-bottom: 3, $margin-top: 0);
}

.body__container--placeholder {
    @include container();
    flex: 0 0 auto;
}

.placeholder__body-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    min-height: 0;
    flex: .5 1 auto;
}

.copy.placeholder__link--volley {
    min-height: 0;
    flex: 1 1 auto;
    display: flex;
    @include vr($margin: 2 0);
}

.placeholder__text-link {
    min-height: 0;
    flex: 1 1 auto;
    display: flex;
    flex-direction: row;
    min-height: 4em;
    align-items: flex-end;
}

.overview__link--jump {
    min-height: 0;
    flex: 1 1 auto;
    display: flex;
    @include vr($margin: 4 0 2 0);
}

.copy.copy__block--placeholder {
    text-align: center;
}

a.home-splash__jump {
    @include mq('desktop-hd') {
        bottom: 5%;
    }
}

