.claim__article--wrapper {
    height: 60em;
    display: flex;
    flex-direction: column;
    position: relative;
    background: $gray-800;
    @include mq('tablet') {
        height: 102em;
    }
    @include mq('desktop') {
        min-height: 100vh;
        flex: 0 1 auto;
        width: 50%;
        max-width: 50%;
        display: flex;
    }
    @include mq('desktop-hd') {}
}

//! Override svg:not(:root) on desktop mq homepage only
