    svg:not(:root) {
        overflow: visible;
        // }
    }
}

.arrow__container {
    display: flex;
    flex-direction: column;
    min-height: 66em;
    flex-wrap: wrap;
    @include mq('tablet') {
        min-height: 102em;
    }
    @include mq('desktop') {
        min-height: 108vh;
        width: 120%;
        margin-left: -5%;
    }
    @include mq('desktop-hd') {
        margin-left: 0;
        min-height: 100vh;
        width: 100%;
    }
}

.arrow-up-svg {
    overflow: visible;
    margin-top: -3em;
    z-index: 2;
    display: flex;
    width: 130%;
    height: 66em;
    margin-left: 6%;
    //   >svg#Layer_1{
    //   min-height: 65em;
    //   flex: auto;
    //   width: 135%;
    //   overflow: visible;
    // }
    @include mq('tablet') {
        min-height: 112em;
        width: 100%;
        height: 110%;
        margin-top: -5em;
        margin-left: 0;
    }
    @include mq('desktop') {
        min-height: 112vh;
        height: 106vh;
        width: auto;
        margin-top: -3vh;
        margin-left: -4vw;
    }
    @include mq('desktop-hd') {
        margin-left: 1vw;
    }
    @include mq('desktop-uhd') {
        margin-left: -1vw;
    }
    svg#Layer_1 {
        filter: drop-shadow(5px 3px 5px #222);
    }
}

.content__block--claim {
    z-index: 3;
    min-height: 66em;
    width: 100%;
    flex: auto;
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 20%;
    >h1 {
        min-height: 4em;
    }
    >div {
        // background: red;
        min-height: 0;
        flex: 0 0 auto;
        display: flex;
        z-index: 8;
        max-width: 83%;
    }
    @include mq('phone') {
        left: 8.5%;
        >div {
            max-width: 74%;
        }
    }
    @include mq('tablet') {
        width: 100%;
        left: 0;
        min-height: 100%;
        flex: 0 0 auto;
        >h1 {
            margin-bottom: unset;
            top: 0;
        }
        >div {
            max-width: 83%;
            justify-content: center;
        }
    }
}

.content__container--claim {
    display: flex;
    flex: 1 1 auto;
    min-height: 0;
}

.claim__container {
    min-height: 50em;
    margin-bottom: -10em;
    left: -20%;
    position: absolute;
    transform: translateY(70%);
    top: -50%;
    z-index: 3;
    width: 48em;
    @include mq('phone') {
        min-height: 75em;
        height: 75em;
        margin-bottom: -5em;
        left: -50%;
        transform: translate(50%, 40%);
        top: -50%;
        width: 100%;
    }
    @include mq('tablet') {
        min-height: 100em;
        height: 100em;
        margin-bottom: 0;
        margin-top: 0;
    }
    @include mq('tablet-wide') {
        flex-direction: column;
    }
    @include mq('desktop') {
        flex-direction: column;
        flex: 0 0 auto;
        // max-width: 61%;
        display: flex;
        width: 100%;
        max-width: 100%;
        flex-wrap: wrap;
        margin-bottom: -10em;
        margin-top: 20em;
        height: 90em;
        min-height: 85em;
    }
    @include mq('desktop-hd') {
        height: 100vh;
    }
}

.pagination.claim__pagination {
    min-height: 2em;
    // top: -15%;
    // left: 49%; 
    // position: absolute;
    // -webkit-transform: translate(-50%,-50%);
    // transform: translate(-50%,-50%);
    @include mq('tablet') {
        margin-bottom: unset;
        min-height: 4em;
        flex: 0 1 4em;
        //     top: 15%;
        // display: flex;
        //   flex: 0 0 0;
        //   position: relative;
        //   top: unset;
        //   left: unset;
        //   transform: unset;
        //   right: .25em;
    }
}

.claim__content-container {
    min-height: 100%;
    @include mq('tablet') {
        position: relative;
        width: 100%;
        max-width: 100%;
    }
    @include mq('tablet-wide') {
        max-width: 100%;
        width: 100%;
    }
    @include mq('desktop') {
        display: flex;
        flex: 0 0 0;
        position: relative;
        top: unset;
        left: unset;
        transform: unset;
        right: .25em;
        height: 100vh;
    }
}

// .claim__background-image--container>svg#Layer_1{
//   @include mq('desktop'){
//     margin-left: 1em;
// }
// }
.claim__title,
.claim__title--emph {
    font-family: $font-family-serif;
    width: 77%;
    margin: auto;
    text-align: center;
    letter-spacing: .10em;
    @include mq('phone-wide') {
        width: 61%;
    }
    @include mq('tablet') {
        width: 44%;
        max-width: 44%;
        margin: 0;
    }
    @include mq('desktop') {
        letter-spacing: .25em;
        font-weight: 300;
    }
}

.claim__title {
    color: $black;
    @include vr($font-size: 2, $line-height: 3.25);
    @include mq('phone') {
        @include vr($line-height: 3.75);
    }
    @include mq('tablet') {
        @include vr($font-size: 2, $line-height: 5);
    }
    @include mq('desktop') {
        @include vr($font-size: 2, $line-height: 5);
    }
}

.claim__title--emph {
    font-size: 3rem;
    @include vr($line-height: 3.25);
    color: $accent;
    font-weight: 400;
    @include mq('phone') {
        @include vr($font-size: 3, $line-height: 3.75);
    }
    @include mq('tablet') {
        @include vr($font-size: 4, $line-height: 5);
    }
    @include mq('desktop') {
        @include vr($font-size: 3, $line-height: 5);
    }
}

.claim__title--small {
    display: none;
    @include mq('desktop') {
        display: flex;
        @include vr($font-size: 2, $line-height: 2);
        letter-spacing: .25em;
        font-weight: 300;
        text-transform: uppercase;
    }
}

.claim__link--jump {
    display: flex;
    position: absolute;
    transform: translateY(95%);
    left: 64%;
    bottom: 0;
    z-index: 3;
    @media only screen and (min-height: 501px) {
        left: 67%;
    }
    @include mq('phablet') {
        left: 50%;
        transform: translate(50%, 95%);
        bottom: 5%;
    }
    @include mq('tablet') {
        left: 45%;
    }
    @include mq('desktop') {
        left: 50%;
    }
}

