        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        flex-direction: row;
        min-height: 100vh;
        @include vr($padding-top: 6);
    }
}

.hybrid__background-graphic-container {
    display: none;
    @include mq('desktop') {
        display: flex;
        position: absolute;
        //     width: 100%;
        max-width: 100%;
        // height: 50vh;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        flex: 1;
        z-index: 0;
        margin-bottom: 0;
    }
}

.hybrid__background-graphic-element {
    flex: 1;
    max-width: 100%;
    min-height: 0;
    height: 100%;
    border: none;
    background: none;
    >svg {
        width: 100%;
        height: 61vh;
    }
    >.hybrid-icon {
        background: none;
        fill: darken($deep-gray, $amount: 10%);
    }
}

.hybrid__pagination {
    color: $primary;
    @include mq('tablet') {
        margin-bottom: unset;
    }
    @include mq('desktop') {
        z-index: 1;
        margin-bottom: unset;
        flex: 0 0 0;
        width: 100%;
        position: absolute;
        left: 10%;
        top: 10%;
    }
}

.hybrid__header-container {
    @include mq('tablet') {
        // flex: 0 0 61%;
        max-width: 61%;
        flex: 0 1 auto;
        text-align: center;
    }
    @include mq('desktop') {
        flex: 0 0 33%;
        max-width: 33%;
        min-height: 32em;
        display: flex;
    }
}

.hybrid__headline-header {
    color: $primary;
    text-align: center;
    @include mq('desktop') {
        max-width: 61%;
    }
}

.hybrid__hr {
    @include mq('desktop') {
        max-width: 61%;
    }
}

.hybrid__deck--header {
    display: flex;
    min-height: 0;
    flex: 0 1 auto;
    color: $primary;
    @include mq('tablet') {
        min-height: 2em;
    }
    @include mq('desktop') {
        max-width: 61%;
    }
}

.hybrid__graphic-container {
    min-height: 8em;
    flex: 0 1 auto;
    display: flex;
    @include mq('tablet') {
        width: 100%;
        max-width: 100%;
        @include vr($margin: 2 0 3 0);
    }
    @include mq('desktop') {
        display: none;
    }
}

.hybrid__graphic-element {
    @include mq('desktop') {
        display: none;
    }
}

.hybrid__body-container {
    @include vr($padding: 0);
    max-width: 90%;
    flex: 0 0 90%;
    @include mq('tablet') {
        flex: 0 0 61%;
        max-width: 61%;
