.services-list__button-container {
    @include mq('desktop') {
        @include vr($margin-top: 2);
    }
}

.services-list__overlay-button {
    color: $black;
    z-index: 1;
    display: flex;
    min-height: 0;
    flex: 0 1 auto;
    background: $white;
    font-variant-caps: all-small-caps;
    letter-spacing: .15rem;
    @include vr($font-size: 1);
    @include pill();
    @include vr($padding: .5 2);
    @include mq('desktop') {
        @include vr($margin-top: 2);
    }
}

.services-list__article-wrapper {
    position: relative;
    margin: 6em 0 0 0;
    padding: 9em 0;
    @include mq('desktop') {
        margin: 0;
        //         height: 138vh;
        display: flex;
        background: $off-white;
        justify-content: center;
        align-content: center;
        flex-direction: column;
    }
    @include mq('desktop-hd') {
        min-height: 145vh;
    }
}

.services-list__background--container {
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
}

.services-list__background--image {
    width: 50%;
    height: auto;
    right: 0;
    bottom: 0;
    position: absolute;
    max-width: unset;
    @include mq('desktop') {
        width: 38%;
    }
    @include mq('desktop-hd') {
        width: 30%;
    }
}

.services-list__content-container {
    width: 90%;
    max-width: 90%;
    display: flex;
    position: relative;
    align-items: center;
    text-align: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: center;
    @include mq('tablet') {
        width: 83%;
        max-width: 83%;
        //       justify-content: center;
        /* width: 83%; */
        max-width: 90%;
    }
    @include mq('tablet-wide') {
        flex-direction: row;
        flex-wrap: wrap;
    }
    @include mq('desktop') {
        width: 50%;
        margin-left: 16%;
