            @include vr($line-height: 2);
            @include mq('tablet-wide') {
                max-width: 83%;
            }
        }
    }
    .subhead__header {
        font-weight: 500;
        text-align: center;
        margin-top: 0;
        @include vr($margin-bottom: 1);
