    .pagination {
        @include vr($margin-top: 4, $margin-bottom: 3);
        // display: inline-block;
        position: relative;
        // left: 50%;
        // transform: translateX(-50%);
        @include mq('tablet-wide') {
            @include vr($margin-top: 6);
        }
    }
    .copy {
        @include mq('tablet-wide') {
            max-width: 91%;
        }
    }
    .flex-container {
        display: flex;
        // flex-wrap: wrap;
        justify-content: center;
        @include mq('tablet-wide') {
            flex-wrap: wrap;
        }
    }
    .flex-item__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        @include vr($padding-top: 3);
        .subhead,
        .copy {
            width: 100%;
        }
    }
    .link__text {
        @include vr($margin-top: 3, $margin-bottom: 3);
    }
}

