.belief {
    padding: 0;
    background: $secondary;
    display: block;
    position: relative;
    height: 90em;
    width: 100%;
    color: $white;
    @include mq('tablet-wide') {
        height: unset;
    }
    @include mq('desktop') {
        @include vr($padding-left: 0, $padding-right: 0);
        height: 100em;
    }
    .flex-container {
        @include mq('tablet-wide') {
            display: flex;
            width: 100%;
        }
    }
    .flex-item__a {
        display: block;
        position: absolute;
        // height: 80em;
        width: 90%;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        z-index: 1;
        @include mq('tablet-wide') {
            display: flex;
            flex-direction: column;
            // min-height: 66em;
            width: 90%;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 1;
            flex: 1;
            max-width: 50%;
            transform: none;
            left: unset;
            top: unset;
            z-index: 0;
            background: $black;
            .pagination {
                @include vr($margin-bottom: 3);
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                width: 64%;
                @include mq('tablet-wide') {
                    justify-content: center;
                }
            }
            .headline-wrapper {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                width: 64%;
            }
            .offering__link {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                width: 64%;
                text-align: center;
                justify-content: center;
            }
        }
        @include mq('desktop') {
            padding: 0 12%;
            text-align: center;
        }
    }
    .flex-item__b {
        filter: brightness(.35);
        position: absolute;
        height: 100%;
        width: 100%;
        display: block;
        z-index: 0;
        top: 0;
        left: 0;
        @include mq('tablet-wide') {
            flex: .75;
            display: flex;
            max-width: 50%;
            height: 80em;
            position: relative;
        }
        @include mq('desktop') {
            height: 100em;
        }
    }
    .background__image--cover {
        background: $secondary;
        img {
            position: absolute;
            height: auto;
            display: block;
            max-width: 400%;
            top: -1%;
            left: -177%;
            width: 400%;
            @include mq('tablet') {
                max-width: 400%;
                top: -5%;
                left: -87%;
                width: 236%;
            }
            @include mq('tablet-wide') {
                max-width: 400%;
                top: -5%;
                left: -102%;
                width: 250%;
            }
            @include mq('desktop') {
                max-width: 400%;
                top: -1%;
                left: -50%;
                width: 154%;
            }
            @include mq('desktop-uhd') {
                left: -62%;
                width: 175%;
            }
        }
        //!end of img
        @include mq('desktop') {
            height: 100%;
        }
    }
    .headline__wrapper {
        @include vr($margin-bottom: 3);
    }
    .headline {
        @include subhead__block();
        @include ultra();
    }
    .copy {
        @include copy__xs();
        @include vr($line-height: 2);
        max-width: 64%;
    }
    .link__text {
        @include vr($margin: 3 0 3 0);
        display: block;
        width: 100%;
        max-width: 100%;
    }
}

.belief__link--jump {
    @include mq('phone',
    $type: max) {
        position: absolute;
        transform: translateY(-50%);
        left: 50%;
        bottom: 5%;
    }
}

