        box-shadow: none;
    }
    @include mq('tablet-wide') {
        flex: 1 1 30%;
        border: none;
        min-height: 70em;
    }
    @include mq('desktop') {
        @include vr($padding: 3 0 12 0);
        flex: 1 1 18%;
        max-width: 23%;
