.svg-cluster {
    g {
        rect,
        .st1 {
            // fill: $white;
        }
    }
    #Group_1061 .st0,
    #Group_1061 .st01 {
        //* cluster outer-rings
        fill: none;
        stroke: none;
    }
    #Group_1061 {
        path.st1 {
            //*cluster circular backgrounds
            fill: none;
            stroke: $black;
            stroke-width: 2;
        }
    }
    g:nth-child(3) {
        //*cluster dial hash marks & indicator
        .st1 {
            fill: $black;
        }
    }
}

.svg-responsive {
    path#_x3C_outline_x3E_ {
        fill: red;
    }
    g#_x3C_cutouts_x3E_ {
        fill: green;
    }
}

.offering__image-container {
