    .svg-rocket,
    .svg-integrative {
        .cls-1 {
            fill: #ffffff;
        }
    }
    .svg-chat {
        .cls-3 {
            fill: #ffffff;
        }
    }
    .svg-rocket {
        // #Performance > path:nth-child(1){
