.diagram__end-to-end{

}
.optimize-diagram__container{
  @include mq('desktop'){
    min-height: 32em;
  }
}
.diagram-item__headline{
  font-variant-caps: all-small-caps;
  letter-spacing: .15rem;
  font-weight: 400;
  @include mq('desktop'){
      @include headline__item();
}
}


.diagram__end-to-end--vertical{
  display: none;
}

.diagram__end-to-end--horizontal{
  display: block;
