//IMAGES===========================================
//splash page mobile background images
#profile-small {
    width: 50%;
    left: 50%;
    bottom: 0;
    position: absolute;
    img#me-small {
        width: 75%;
    }
    @include mq('phone-wide') {
        width: 35%;
        left: 65%;
    }
    @include mq('tablet-small') {
        // width: 75%;
        // margin-left: 60%;
    }
}

#bell-small {
    // max-width: 61.8%;
    max-width: 38%;
    display: block;
    @include push-auto();
    overflow: hidden;
    @include mq('tablet') {
        max-width: 38%;
    }
}

#register-small {
    position: absolute;
    overflow: hidden;
    width: 100%;
}

.register-small {
    margin-left: auto;
    margin-right: 3em;
    display: block;
    width: 38.2%;
    @include mq('phone') {}
    @include mq('phone-wide') {}
    @include mq('phablet') {
        margin-right: 10%;
    }
    @include mq('tablet-small') {}
    @include mq('tablet') {}
    @include mq('tablet-wide') {}
    @include mq('desktop') {}
    @include mq('desktop-wide') {}
    @include mq('desktop-hd') {}
    @include mq('desktop-uhd') {}
}

#my-conductor {
    width: 100%;
    bottom: 0;
    position: absolute;
    img.conductor-small {
        width: 185%;
        margin-left: 2rem;
    }
    @include mq('tablet-small') {
        width: 75%;
        margin-left: 60%;
    }
    @include mq('tablet') {
        width: 100%;
        height: auto;
        margin-left: 0;
        img.conductor-small {
            width: 250%;
            height: auto;
            margin-left: 0rem;
        }
    }
    @include mq('tablet-wide') {
        bottom: -12em;
        img.conductor-small {
            margin-top: 10em;
        }
    }
}

#my-brains {
    bottom: 0;
    left: 5%;
    width: 96%;
    position: fixed;
    @include mq('tablet-small') {
        bottom: 0;
        margin-left: 20%;
        width: 80%;
        position: fixed;
    }
}

.evo2small {
    flex: 0 0 auto;
    position: relative;
    bottom: 0;
    display: block;
    margin: 0 auto;
    width: 80%;
    @include mq('phone') {
        width: 75%;
        margin-left: 12.5%;
        @include mq('tablet-wide') {
            width: 165%;
            margin-left: 0;
        }
        @include mq('desktop') {
            min-height: 32em;
            width: auto;
        }
    }
}

#my-shoes {
    width: 100%;
    overflow: hidden;
    bottom: 0;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    margin-left: auto;
    z-index: -1;
    @include mq('tablet-wide') {
        width: 83%;
        max-width: 83%;
        right: -0em;
        bottom: 1.25em;
    }
    @include mq('desktop') {
        z-index: 4;
    }
}

//--reminder--image is flipped so everything is backwards
#shoes-img {
    display: block;
    width: 61%;
}

@include mq('tablet-wide') {
    #fam-container {
        overflow: hidden;
    }
    #fam-img {
        width: 150%;
        max-width: 150%;
    }
}

