//svg tweaks
.stats .style-svg {
    // height: $ms-6;
    // width: $ms-6;
    @include mq('tablet') {}
}

svg#services__arrow--img {
    fill: $primary;
    stroke-linecap: round;
    stroke-miterlimit: 10;
    display: block;
}

.icon__svg,
.icon__svg .cls-2,
.icon__svg .cls-1 {
    fill: $black;
}

#overlays_2_ {
    fill: $primary;
}

.arrow-dwn-svg {
    .st0 {
        fill: rgba(20, 20, 20, .9);
        stroke: $black;
    }
}

#svg__blue_a2--bg {
    #A_outline {
        stroke: $primary;
    }
    #dots,
    #solid_dots {
        fill: $primary;
    }
    #grid,
    #pattern {
        stroke: #76D4F4;
    }
}

#svg__blue_a2--small {
    #A_outline {
        stroke: $primary;
    }
    #pattern,
    #grid {
        stroke: $gray-300;
    }
    #solid_dots,
    #dots {
        fill: $primary;
    }
}

#globe .st0 {
    stroke: none;
}

#accessible {
    fill: #fff;
}

.svg__skills {
    // fill: $capabilities-overlay-icon-fill-color;
    fill: $primary;
    .cls-1,
    .cls-2 {
        // fill: $capabilities-overlay-icon-fill-color;
        fill: $primary;
    }
}

#bell__small_svg {
    svg#Layer_1 {
        fill: $black;
    }
}

// diagram & graphics svg's
.diagram__end2end {
    .cls-1,
    .cls-2 {
        fill: $off-white;
    }
}

svg.chev--dwn {
    stroke: #111;
    stroke-linecap: round;
    stroke-width: .5em;
}

#digits {
    color: $qualifications-icon-fill-color;
}

.cls-1 {
    fill: $qualifications-icon-fill-color;
}

// objectives-panel__diagram_svg-tweaks 
.delta--down,
.delta--right {
    .cls-1 {
        fill: $gray-400;
    }
}

.proof-quals .cls-1 {
    fill: $off-white;
}