$font-family-sans-serif:    geomanist, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$font-family-monospace:     Consolas, "Courier New", SFMono-Regular, Menlo, Monaco, "Liberation Mono", monospace;
$font-family-serif:         adobe-garamond-pro, Georgia, Palatino, "Times New Roman", Courier;

$font-weight-thin:          100;
$font-weight-light:         200;
$font-weight-normal:        300;
$font-weight-medium:        400;
$font-weight-bold:          500;
$font-weight-black:         600;

$headline-font-family:      $font-family-sans-serif;
$subhead-font-family:      	$font-family-sans-serif;
$sublist-font-family:      	$font-family-sans-serif;
$display-text-font-family:  $font-family-sans-serif;
$body-text-font-family:     $font-family-serif;
$para-font-family:          $font-family-serif;
$lead-font-family:          $font-family-sans-serif;
$link-font-family:			$font-family-monospace;
$nav-font-family:			$font-family-monospace;


$headline-font-weight:      600;
$subhead-font-weight:       400;
$sublist-font-weight:       500;
$display-text-font-weight:	300;
$body-text-font-weight:     300;
$para-font-weight:          300;
$lead-font-weight:          400;

